import { h } from 'preact';
import style from './story.module.css';

const TheStory = () => (
  <div class={style.container}>
    <h1>The Story</h1>
    <div class={style.content_container}>
      <p>There is nothing more beautiful than turning your anxiety, stress, or your abundance of energy into a loving, welcoming, and unique piece of art. In 2011, my father was overcoming a few anxiety attacks and decided to make a change in himself and channel his energy and worries into creating a place that could make people feel safe, love, and a part of a community.</p>
      <p>If you meet my dad you'll wonder “Who is this guy?” “What's he thinking?” and as you continue to wonder you'll notice, that like most eccentric people, he is someone uber smart who can truly turn anything into gold.</p>
      <p>As per usual, my dad wakes up at 4AM for a walk while listening to some of his favorite Greek, Italian, or Israeli music. He lives in Yuma Arizona, and at the time he had started a Drone Flying Club in the desert for "Snowbirds" traveling to Arizona from Canada to escape the cold winter. This club had given him a sense of purpose, which was to create a place for people who love adventure and community to meet, drink cold beers, eat some pizza, and act like a child again. On one of his early morning walks he noticed something shiny sitting on a ranch, surrounded by flowers, water, and horses. My dad walked up to the structure and noticed it was a 1959 vintage Streamline RV. Everything was original and PINK! Pink stove, pink shower and pink bed layouts. It didn't have much more to it, but my Dad could always see potential in one man's trash. He knocked on the RV owners' door and asked to buy the Streamline – the owner quickly said, "No. This is a gem and I've owned it for 50 years." Respectfully my father left, but he thought about that Streamline RV on every morning walk for the next 2 years.</p>
      <p>One day the RV owner happened to be outside and noticed my dad walking by. He called my dad over and said “ I'll sell it to you, as long as you make it shiny, keep it pink, and send us pictures monthly” ... And that is where I come in. My father called me and said, "I need money to buy something magical."</p>
      <p>When you're raised your whole life with a dad like mine, you learn quickly that some of his crazy ideas really are magic, and so you learn to just trust. That was the day I bought my very first vintage RV -- Pinky.</p>
      <p>My dad kept his promise to the owner and he, alone, every day for four months shined the outside of Pinky until it looked new! The daily work of shining Pinky (imagine wax on wax off from Karate Kid)  gave my dad new purpose and he was really starting to feel his anxiety under control. This new hobby, sense of purpose, creative outlet, and routine had become something my dad needed and once Pinky was finished,  we invested and started collecting two, three, and then four trailers... Always vintage, always turning one person's trash into another person's treasure. We had no plan other than to keep them, have my dad work on the restoration,  and use them on our family trips (we are a family of six!).</p>
      <p>When the pandemic hit in 2020 we heard of many people losing their jobs and not having a place to go that was affordable. We decided to share our streamliners with local artist to stay for free and create a space for their creative outlet. That's when I met cowboy, artist, and poetry writer, Josh, and yogi, professional skater, and artist, Stephanie. They needed a creative outlet, an income, and a place to spend time in. We shared our vision of creating a space that will help people distract themselves from the pandemic while also providing  a blank canvas for self expression and a way to connect back to their free, happy, and lively side. That's when, with the help of these local artists, we started to recreate the interior spaces and make the trailers what they are today; shiny, groovy and out there!</p>
      <p>I personally have traveled all over the world and not being able to travel during the pandemic was difficult on my sprits (and many others'). I have always loved the south of France, the feeling of open spaces, wineries, sunsets that truly take your mind to a state of happiness. That was what I was longing for. I went on a search to find wineries that wanted to feature a unique experience for guests to stay in and I was lucky enough to meet and partner with John and Kambrah, owners of the most beautiful land just steps away from the best wines in Temecula, California, and Fredericksburg, Texas.</p>

      <p>We welcomed each guest with a bottle of wine, set the record player to play French love songs, and started our adventure of sharing our unique trailers with the wino world! Since we opened, we have welcomed hundreds of guests on Airbnb, Hipcamp, VRBO and Exchanges. We're looking forward to growing our fleet and providing art therapy as a creative outlet to those who need it, and an unforgettable experience for our Glamping on Wine guests!</p>
    </div>
  </div>
);

export default TheStory;
